<template>
  <v-app>
    <transition name="fade" mode="out-in">
			<component :is="layout" />
		</transition>
  </v-app>
</template>

<script>

import { mapActions, mapGetters } from "vuex";

import LoadingLayout from "./layouts/LoadingLayout.vue";
import LoginLayout from './layouts/LoginLayout.vue';
import MainLayout from './layouts/MainLayout.vue';

export default {
  name: "App",
  data: () => ({
		loading: true,
	}),
  computed: {
		layout() {
			return this.loading ? 
        "LoadingLayout" : this.token == null ? 
        "LoginLayout" : "MainLayout";
		},
		...mapGetters({
			token: "token",
		}),
	},
  methods: {
      ...mapActions(["refresh"]),
  },
  components: {
		LoadingLayout,
    LoginLayout,
    MainLayout
	},
  async mounted() {
    await this.refresh()
		this.loading = false;
	},
}
</script>
